@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    scroll-behavior: smooth;
}

.center {
    min-height : 100vh;
    background-color: rgb(42, 41, 43);
    color : rgb(187, 138, 138);
    padding-top : 130px;
    padding-bottom : 70px;
}



@media (max-width : 700px) {
    .app {
        padding-top : 70px;
    }
}

.mainPages {
    text-transform: uppercase;
    display: flex;
    justify-content: center;
    font-family: "Montserrat", sans-serif;
    font-size: 2.5vw;
    padding : 8px;
    border-radius: 50px;
    color : white
}

@media (max-width : 1000px) {
    .mainPages {
        font-size: 6.4vh;
    }
}
.header {
    width : 100%;
    cursor : pointer;
    position: fixed;
    display: flex;
    justify-content: center;
    background-color: rgb(31, 136, 241);
    font-family: 'Montserrat', sans-serif;
    font-size: 3vw;
    padding-top: 15px;
    padding-bottom: 15px;
    box-shadow: 0px 5px 5px rgb(165, 164, 161);
    color: rgb(256,256,256);
    z-index: 100;
}

@media (max-width : 1000px) { 
    .header {
        font-size: 5.5vh;
    }
}
.node {
    display: flex;
    flex-direction: column;
    width: 200px;
    padding: 5px;
    margin: 10px 0px;
    background-color: black;
    border-radius: 10px;
    position: relative;
    font-family: "Montserrat",sans-serif;
}

.node:hover {
    background-color : white;
    color : blue;
}

@media (max-width : 550px) {
    .node {
        width: 46%;
    }
}

.poster {
     border-radius : 10px; 
}

.title {
    width: 100%;
    text-align: center;
    font-size: 15px;
    padding: 8px 0;
}

.details  {
    display: flex;
    justify-content: space-between;
    padding-bottom: 3px;
    padding : 0 2px;
    padding-bottom: 3px;
}

.carouselItem {
    display: flex;
    flex-direction: column;
    object-fit: contain;
    padding: 10px;
  }
  .carouselItem__img {
    border-radius: 10px;
    margin-bottom: 5px;
    box-shadow: 0px 0px 5px olivedrab;
  }
.NodeModal__landscape {
  object-fit: contain;
  border-radius: 10px;
}

.NodeModal__portrait {
  display: none;
  object-fit: contain;
  border-radius: 10px;
}

.tagline {
  padding-bottom: 10px;
  align-self: center;
}

.NodeModal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
  scrollbar-width: none;
}

.NodeModal::-webkit-scrollbar {
  display: none;
}

.NodeModal__about {
  padding: 10px;
  width: 95%;
  height: 90%;
  display: flex;
  flex-direction: column;
  font-family: "Roboto", sans-serif;
  justify-content: space-evenly;
  font-weight: 300;
}

.NodeModal__title {
  height: 12%;
  font-size: 5vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

.NodeModal__description {
  display: flex;
  height: 30%;
  overflow-y: scroll;
  padding: 15px;
  border-radius: 20px;
  scrollbar-width: thin; /* Firefox */
  box-shadow: inset 0 0 10px rgb(256,256,256);
  text-align: justify;
}

.NodeModal__description::-webkit-scrollbar {
  display: none;
}

@media (min-width: 835px) {
  .NodeModal__landscape {
    display: none;
  }
  .NodeModal__portrait {
    display: flex;
    width: 38%;
  }
  .NodeModal {
    flex-direction: row;
    justify-content: space-around;
    padding: 10px 0;
  }
  .NodeModal__about {
    width: 58%;
    padding: 0;
    height: 100%;
  }
  .NodeModal__title {
    font-size: 3.5vw;
  }
  .NodeModal__description {
    font-size: 22px;
  }
}
.trending {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
}
.paging {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 15px;
}
